function Self(){
	return(
		<div style={{height:"500px"}} className="bg-light-green fl dib br3 pa3   sans-serif grow">
			<div style={{margin:"0px 33%"}} className="tc br3 bg-lightest-blue">
				<h2>Self-Analysis</h2>
			</div>
			<div style={{width:"18%",height:"88%"}} className="pa4 bg-pink br3">
				<img alt="Personal_Pic" src={tech} width="100%" height="100%"/>
			</div>
		</div>
		)
}
export default Self